*, *::before, *::after {
  box-sizing: border-box;
}

* {
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  min-height: 100vh;
}

img, svg, picture, video {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

h2 {
  font-size: 2rem;
  line-height: 1.2190625;
}

h3 {
  font-size: 1.125rem;
  line-height: 1.5555555556;
}

body {
  font-size: 0.9375rem;
  line-height: 1.8666666667;
  font-family: "Montserrat", sans-serif;
  color: rgb(110, 114, 142);
}

a {
  text-decoration: none;
  color: inherit;
}

.component__header {
  text-align: center;
}
.component__title {
  margin-bottom: 2.5rem;
}
.component__controls {
  border: none;
}
.component__content {
  margin-top: 5rem;
}

.pricing-toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;
}
.pricing-toggle label {
  color: rgba(110, 114, 142, 0.5);
  cursor: pointer;
}
.pricing-toggle input[type=radio] {
  display: inline-block;
  width: 50%;
  height: 100%;
  opacity: 0;
  position: relative;
  z-index: 1;
  cursor: pointer;
}
.pricing-toggle input[type=radio]:focus ~ .pricing-toggle__switcher {
  outline: 3px solid rgb(74, 77, 96);
}
.pricing-toggle__wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3.5rem;
  height: 2rem;
  border-radius: 1rem;
  background-image: linear-gradient(135deg, rgb(105, 110, 221) 0%, rgb(162, 167, 240) 100%);
  transition: all 250ms ease-out;
}
.pricing-toggle__wrapper:hover {
  background-image: linear-gradient(135deg, rgba(105, 110, 221, 0.5) 0%, rgba(162, 167, 240, 0.5) 100%);
}
.pricing-toggle__switcher {
  display: block;
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  z-index: 0;
  background-color: rgb(255, 255, 255);
  transition: all 250ms ease-out;
}
.pricing-toggle #monthly:checked ~ .pricing-toggle__switcher {
  right: 0.25rem;
  left: 50%;
}

.plan {
  padding: 1.9375rem 1.8125rem;
  text-align: center;
  border-radius: 0.625rem;
  box-shadow: 0 1.25rem 2.5rem 0 rgba(212, 210, 244, 0.5);
  background-color: rgb(255, 255, 255);
  width: calc(100% - 3rem);
  max-width: 21.875rem;
  margin-inline: auto;
}
.plan__title {
  margin-bottom: 1.5rem;
}
.plan__price {
  font-size: 4.5rem;
  line-height: 0.9861111111;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  margin-bottom: 2rem;
}
.plan__price span {
  font-size: 2.5rem;
  line-height: 1.219;
}
.plan__features {
  list-style: none;
}
.plan__feature {
  border-top: 1px solid rgba(110, 114, 142, 0.25);
  padding-block: 0.75rem;
}
.plan__feature:last-child {
  border-bottom: 1px solid rgba(110, 114, 142, 0.25);
}
.plan__button {
  border: none;
  border-radius: 0.375rem;
  background: linear-gradient(135deg, rgb(162, 167, 240) 0%, rgb(105, 110, 221) 100%);
  width: 100%;
  margin-top: 2rem;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  letter-spacing: 0.0875rem;
  padding: 0.875rem;
  cursor: pointer;
  border: 1px solid transparent;
  position: relative;
  transition: all 250ms ease-in-out;
  overflow: hidden;
  z-index: 1;
}
.plan__button:hover {
  border-color: rgb(109, 114, 222);
  color: rgb(109, 114, 222);
}
.plan__button::after {
  content: "";
  position: absolute;
  inset: 0;
  background-color: rgb(255, 255, 255);
  opacity: 0;
  transition: all 250ms ease-in-out;
  z-index: -1;
}
.plan__button:hover::after {
  opacity: 1;
}

body {
  background: rgb(246, 246, 254);
  padding-block: 4rem 4.4375rem;
}
@media (min-width: 45rem) {
  body {
    padding-block: 4.4375rem 6.4375rem;
  }
}